html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  font-family: 'Roboto', sans-serif;
  text-align: center;
  font-weight: lighter;
}

a {
  color: inherit;
}

button {
  cursor: pointer;
  outline: none;
  font-weight: 300;
  background-color: #1dce94;
  border: none;
  border-radius: 500px;
  color: white;
  padding: 0.5rem 1rem;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

button:hover {
  color: #1dce94;
  background-color: white;
}

button:active {
  background-color: #cbcbcb;
}

.splash {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
  width: 100%;
}

.splash__content {
  position: relative;
  top: 5%;
  right: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.5rem 1rem;
  max-width: 480px;
}

.splash-title {
  margin-bottom: 1rem;
  max-width: 200px;
  height: auto;
}

.splash-title__img {
  height: auto;
  width: 100%;
  color: white;
  margin: 0;
  padding: 0.5rem 1rem;
}

.splash-overview {
  background-color: white;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.1), 0 10px 25px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.1), 0 10px 25px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 4rem;
}

.splash-overview-footer {
  padding: 2rem;
  background-color: white;
  font-size: 80%;
}

.splash-overview__heading {
  font-size: 150%;
  padding: 1.5rem;
  margin: 0;
}

.splash-overview__star {
  margin-bottom: 2rem;
}

.splash-overview__details {
  background-color: #323bff;
  color: white;
  padding: 2rem;
}

.splash-overview-plugins {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 2rem;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 1rem;
}

.splash-overview-plugins__list {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
  padding-top: 1.4rem;
  -webkit-transition: all 1s ease-out;
  transition: all 1s ease-out;
  display: none;
}

.splash-overview-plugins__item {
  margin-bottom: 1rem;
}

.splash-overview__conclusion {
  background-color: #ff4970;
  color: white;
  padding: 2rem;
}

.splash-overview-start {
  font-size: 80%;
  background-color: #022b36;
  color: #c6c6c6;
  padding: 2rem;
  text-align: left;
}

.splash-overview-start__command {
  margin-bottom: 0.3rem;
}

.splash-overview-start__command:before {
  content: '$';
  font-weight: bold;
  margin-right: 0.2rem;
}

.splash-overview-start__comment {
  color: #A3A3A3;
}

.splash-overview-issues {
  background-color: #fd8c68;
  color: white;
  padding: 2rem;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.video-background {
  background: #000;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -99;
}

.video-foreground,
.video-background iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

@media (min-aspect-ratio: 16 / 9) {
  .video-foreground {
    height: 300%;
    top: -100%;
  }
}

@media (max-aspect-ratio: 16 / 9) {
  .video-foreground {
    width: 300%;
    left: -100%;
  }
}

